.woocommerce-product-search {

	input[type="search"] {
		width: 100%;
		padding: $small-spacing;
		transition: border $speed $cubic-bezier;
		border: 3px solid $black;
		border-radius: $base-border-radius;
		background-color: transparent;
		color: $white;

		@include media(">=screen960") {
			@include hocus {
				border-color: $black--lightest;
				outline-color: $yellow;
				outline-offset: 4px;
				outline-width: 2px;
				box-shadow: none;
			}
		}
	}
}

.woocommerce-variation-add-to-cart {
	input[type="number"] {
		padding: $small-spacing;
		transition: border $speed $cubic-bezier;
		line-height: $base-line-height;
		border: 3px solid $black;
		border-radius: $base-border-radius;
		background-color: transparent;
		color: $black;

		@include media(">=screen960") {
			@include hocus {
				border-color: $black--lightest;
				outline-color: $yellow;
				outline-offset: 4px;
				outline-width: 2px;
				box-shadow: none;
			}
		}
	}
	button.button.alt {
		padding-top: $small-spacing;
		padding-bottom: $small-spacing;
		transition: background $speed $cubic-bezier,
					border $speed $cubic-bezier,
					color $speed $cubic-bezier;
		font-weight: 800;
		line-height: $base-line-height;
		border: 3px solid $yellow;
		border-radius: $base-border-radius !important;
		color: $black;

		&.disabled {
			opacity: 1 !important;
			border-color: $white--darker;
			background-color: $white--darker;
			color: $gray;
		}

		&:not(.disabled) {
			@include hocus {
				border-color: $yellow--dark;
				background-color: $yellow--dark;
			}
			&:active {
				background-color: $black;
				color: $white;
			}
		}
	}
}

.woocommerce-cart-form {

	input[type="text"],
	input[type="number"] {
		padding: $small-spacing;
		font-family: $font-main;
		border: 3px solid $black;
		border-radius: $base-border-radius;
		background-color: transparent;
		color: $black;
	}

	.product-name a {
		@include inline-block();
		text-decoration: none !important;
	}

	.coupon .button {
		@include btn-style();
		padding-top: 1em !important;
		padding-bottom: 1em !important;
		background-color: transparent;
	}
}

.select2-dropdown.select2-dropdown--below {
	border: 3px solid $black;
	border-top: 0;
}

.select2-container--default .select2-search--dropdown .select2-search__field {
	border: 3px solid $black;
	border-radius: 3px;
	background-color: $white;
}
.select2-container--default .select2-results__option[aria-selected=true] {
	background-color: $white--darker;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
	background-color: $yellow;
	color: $black;
}
